import React, { useState, useRef } from 'react';
import axios from 'axios';
import { Card, CardContent, Typography, Button, Alert, Link, Stack, Box, LinearProgress, Snackbar, IconButton } from '@mui/material';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';

const UploadPage = () => {
  const [file, setFile] = useState(null);
  const [isUploading, setIsUploading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [downloadUrl, setDownloadUrl] = useState('');
  const [uploadProgress, setUploadProgress] = useState(0);
  const [isDragActive, setIsDragActive] = useState(false);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });
  const fileInputRef = useRef(null);

  const MAX_BYTES = 50 * 1024 * 1024; // 50MB

  const formatFileSize = (bytes) => {
    if (bytes === 0 || !Number.isFinite(bytes)) return '0 B';
    const units = ['B', 'KB', 'MB', 'GB', 'TB'];
    const idx = Math.min(units.length - 1, Math.floor(Math.log(bytes) / Math.log(1024)));
    const value = bytes / Math.pow(1024, idx);
    return `${value.toFixed(2)} ${units[idx]}`;
  };

  const handleFileSelect = (event) => {
    const selectedFile = event.target.files[0];
    if (selectedFile) {
      if (selectedFile.size > MAX_BYTES) {
        setFile(null);
        setDownloadUrl('');
        setSuccess('');
        setError('File is larger than 50MB limit. Please choose a smaller file.');
        return;
      }
      setFile(selectedFile);
      setError('');
      setSuccess('');
    }
  };

  const handleUpload = async () => {
    if (!file) {
      setError('Please select a file to upload');
      return;
    }

    setIsUploading(true);
    setError('');
    setUploadProgress(0);

    try {
      const formData = new FormData();
      formData.append('file', file);

      const response = await axios.post('https://fantastic-space-sniffle-g4ww546x759r394rg-5000.app.github.dev/api/upload', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        onUploadProgress: (event) => {
          if (event.total) {
            const percent = Math.round((event.loaded * 100) / event.total);
            setUploadProgress(percent);
          }
        }
      });

      if (response.data.success) {
        setSuccess('File uploaded successfully!');
        setDownloadUrl(response.data.downloadUrl);
        setSnackbar({ open: true, message: 'Upload complete', severity: 'success' });
      }
    } catch (error) {
      setError(error.response?.data?.error || 'Upload failed. Please try again.');
      setSnackbar({ open: true, message: 'Upload failed. Please try again.', severity: 'error' });
    } finally {
      setIsUploading(false);
      setUploadProgress(0);
    }
  };

  const resetForm = () => {
    setFile(null);
    setError('');
    setSuccess('');
    setDownloadUrl('');
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  const dashedBorder = '2px dashed rgba(255,255,255,0.2)';
  const panelBg = 'rgba(255,255,255,0.04)';

  return (
    <Card elevation={6} sx={{ backgroundColor: 'background.paper' }}>
      <CardContent>
        <Typography variant="h5" fontWeight={700} textAlign="center" gutterBottom>
          Upload File
        </Typography>

        <Box mb={3}>
          <input
            ref={fileInputRef}
            type="file"
            onChange={handleFileSelect}
            id="file-upload"
            style={{ display: 'none' }}
          />
          <label htmlFor="file-upload">
            <Box
              sx={{
                height: 140,
                border: dashedBorder,
                borderRadius: 2,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                cursor: 'pointer',
                backgroundColor: isDragActive ? 'rgba(255,255,255,0.08)' : panelBg,
                '&:hover': { backgroundColor: 'rgba(255,255,255,0.06)' }
              }}
              onDragOver={(e) => { e.preventDefault(); setIsDragActive(true); }}
              onDragLeave={() => setIsDragActive(false)}
              onDrop={(e) => {
                e.preventDefault();
                setIsDragActive(false);
                const dropped = e.dataTransfer.files?.[0];
                if (dropped) {
                  if (dropped.size > MAX_BYTES) {
                    setFile(null);
                    setDownloadUrl('');
                    setSuccess('');
                    setError('File is larger than 50MB limit. Please choose a smaller file.');
                    return;
                  }
                  setFile(dropped);
                  setError('');
                  setSuccess('');
                }
              }}
            >
              <Stack spacing={1} alignItems="center">
                <CloudUploadIcon color="primary" sx={{ fontSize: 34 }} />
                <Typography variant="body2" color="text.secondary">
                  Click to upload or drag and drop
                </Typography>
                <Typography variant="caption" color="text.disabled">
                  Any file type (MAX. 50MB)
                </Typography>
              </Stack>
            </Box>
          </label>
        {file && (
            <Box sx={{ mt: 1.5, p: 1.5, borderRadius: 1, backgroundColor: panelBg }}>
              <Typography variant="body2" sx={{ fontWeight: 600 }}>
                {file.name}
              </Typography>
              <Typography variant="caption" color="text.secondary">
                {file.type || 'Unknown type'} · {formatFileSize(file.size)}
              </Typography>
              <Box sx={{ mt: 1 }}>
                <LinearProgress variant="determinate" value={Math.min(100, Math.round((file.size / MAX_BYTES) * 100))} />
                <Typography variant="caption" color="text.secondary">
                  {Math.min(100, Math.round((file.size / MAX_BYTES) * 100))}% of 50MB
                </Typography>
              </Box>
            </Box>
          )}
        </Box>

        {uploadProgress > 0 && isUploading && (
          <Box sx={{ mb: 3 }}>
            <LinearProgress variant="determinate" value={uploadProgress} />
            <Typography mt={1} variant="caption" color="text.secondary">{uploadProgress}%</Typography>
          </Box>
        )}

        {downloadUrl && (
          <Box sx={{ mb: 3, p: 2, borderRadius: 2, backgroundColor: 'rgba(26, 26, 40, 0.9)', border: '1px solid rgba(66,165,245,0.18)' }}>
            <Typography variant="body2" sx={{ mb: 1, color: 'primary.light' }}>Your download link:</Typography>
            <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2} alignItems="flex-start">
              <Link href={downloadUrl} target="_blank" rel="noreferrer" underline="hover" sx={{ wordBreak: 'break-all', flex: 1 }}>
                {downloadUrl}
              </Link>
              <Stack direction="row" spacing={1}>
                <Button size="small" startIcon={<ContentCopyIcon />} onClick={() => { navigator.clipboard.writeText(downloadUrl); setSnackbar({ open: true, message: 'Link copied', severity: 'success' }); }}>Copy</Button>
                <Button size="small" variant="outlined" endIcon={<OpenInNewIcon />} onClick={() => window.open(downloadUrl, '_blank')}>Open</Button>
              </Stack>
            </Stack>
          </Box>
        )}

        {error && (
          <Alert severity="error" sx={{ mb: 2 }}>
            {error}
          </Alert>
        )}
        {/* success message is shown via Snackbar toast */}

        <Stack direction="row" spacing={2}>
          <Button onClick={handleUpload} disabled={!file || isUploading} variant="contained" fullWidth>
            {isUploading ? 'Uploading...' : 'Upload'}
          </Button>
          <Button onClick={resetForm} variant="outlined">Reset</Button>
        </Stack>
      </CardContent>
      <Snackbar
        open={snackbar.open}
        autoHideDuration={2000}
        onClose={() => setSnackbar({ ...snackbar, open: false })}
        message={snackbar.message}
        action={
          <IconButton size="small" aria-label="close" color="inherit" onClick={() => setSnackbar({ ...snackbar, open: false })}>
            ✕
          </IconButton>
        }
      />
    </Card>
  );
};

export default UploadPage;
